<template>
<!-- 持 仓 页 -->
  <!--      下部区域-->
      <div class="bottom">
<!--        底部数据展示区域-->
        <div class="item" v-for="(item,index) in heyuedelegateList" :key="index">
         <!-- thead  部分 -->
         <div class="thead">
             <div class="thead-left">
                 <img src="../../assets/image/capital/btb.png" alt="" class="thead-left-img">
                 <span class="thead-left-text">币种：{{item.coin}}</span>
             </div>
             <div class="thead-right-btn green-bg green-font">
                 {{item.type}}
             </div>
         </div>
         <!-- 数量区域 -->
         <div class="num-box">
             <div class="num-left">
                <span>价格：</span>
                <span>{{item.price}} USDT</span>
             </div>
             <div class="num-right">
                <span>{{format(item.create_time)}}</span>
             </div>
         </div>
        </div>
      </div>
</template>

<script>
import Vue from 'vue'
import { heyuedelegate } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import { DropdownMenu, DropdownItem, Icon, Toast } from 'vant'
Vue.use(DropdownMenu).use(Icon).use(DropdownItem).use(Toast)
export default {
  components: {},
  data () {
    return {
      value1: 0,
      value2: 'a',
      option1: [
        { text: 'BTC/USDT', value: 0 },
        { text: 'ETH/USDT', value: 1 },
        { text: 'LBTH/USDT', value: 2 }
      ],
      heyuedelegateList: [],
      heyuedelegateData: {
        u_id: ''
      }
    }
  },
  created () {
    this.heyuedelegateData.u_id = sessionStorage.getItem('u_id')
    this.heyuedelegateFun()
  },
  methods: {
    add0 (m) { return m < 10 ? '0' + m : m },
    format (isTime) {
      var times = isTime * 1000
      var time = new Date(times)
      var y = time.getFullYear()
      var m = time.getMonth() + 1
      var d = time.getDate()
      // var h = time.getHours()
      // var mm = time.getMinutes()
      // var s = time.getSeconds()
      // console.log(y + '-' + this.add0(m) + '-' + this.add0(d) + ' ' + this.add0(h) + ':' + this.add0(mm) + ':' + this.add0(s))
      return y + '-' + this.add0(m) + '-' + this.add0(d)
    },
    heyuedelegateFun () {
      var that = this
      var data = objKeySort(this.heyuedelegateData)
      var token = sgin(data)
      this.heyuedelegateData.token = token
      heyuedelegate(this.heyuedelegateData).then(res => {
        console.log(res)
        if (res.code === 0) {
          that.heyuedelegateList = res.data
          that.isShow = false
          delete that.heyuedelegateData.token
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        delete that.heyuedelegateData.token
      })
    }
  }
}
</script>
